@font-face {
  font-family: Avenir;
  font-weight: 300;
  src: url("Avenir-Book.657c3568.ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: 700;
  src: url("Avenir-Heavy.01d9ab74.ttf");
}

:root {
  --font-weight: 300;
  --border-color: #e1e7ed;
  --text-color: #3d4e61;
}

body {
  font-family: Avenir, Arial, sans-serif;
  font-weight: var(--font-weight);
  color: var(--text-color);
  margin-top: 0;
  font-size: 16px;
}

.form {
  flex-direction: column;
  padding-top: 20px;
  display: flex;
}

.form__input {
  box-sizing: border-box;
  border: 1px solid var(--border-color);
  font-size: inherit;
  font-family: inherit;
  font-weight: inherit;
  background-image: url("Search.ccd3c07b.svg");
  background-position: 26px;
  background-repeat: no-repeat;
  background-size: 19px 19px;
  border-radius: 4px;
  margin-bottom: 20px;
  padding-left: 62px;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__input:hover {
  box-shadow: 0 4px 4px #00000040;
}

.form__input:focus {
  font-family: inherit;
  font-weight: 900;
  font-size: inherit;
  background-image: url("Search.ccd3c07b.svg"), linear-gradient(#fff 0%, #f6f6f7 100%);
  background-position: 26px, 0 0;
  background-repeat: no-repeat;
  background-size: 19px 19px, 100% 100%;
  outline: none;
}

.form__input--big {
  height: 70px;
}

.form__input--small {
  background-position: 13px;
  background-size: 11px 11px;
  height: 42px;
